:root {
  --color-primary: #ffa07a;
  --color-white: #fbf7f0;
  --color-black: #191919;
  --color-grey-100: #2b2b2b;
  --color-grey-200: #242424;
  --color-pink-100: #dcc6e0;
}

body {
  background-color: var(--color-white);
  color: var(--color-black);
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
  font-size: 16px;
  line-height: 1.5;
}

* {
  box-sizing: border-box;
}

a.cta {
  background-color: var(--color-primary);
  border: 0;
  border: 2px solid var(--color-primary);
  color: var(--color-grey-100);
  cursor: pointer;
  border-radius: 6px;
  margin-top: 24px;
  padding: 6px 14px;
  text-decoration: none;
  transition: border-radius .2s ease-in-out;
  display: inline-block;
}

a.cta:hover {
  border-radius: 8px;
}

.logo {
  text-align: center;
  margin-top: 128px;
  margin-bottom: 12px;
  font-size: 54px;
  font-weight: 900;
}

@media (width <= 700px) {
  .logo {
    margin-top: 40px;
  }
}

.logo span {
  color: var(--color-grey-100);
  font-weight: normal;
}

.description {
  text-align: center;
  max-width: 550px;
  margin: 0 auto 100px;
  padding: 0 18px;
}

@media (width <= 700px) {
  .description {
    margin-bottom: 40px;
  }
}

.punchline {
  text-align: center;
  margin-top: 0;
  font-size: 20px;
  font-weight: 700;
}

